.column {
  display: flex;
  flex-direction: column;
}
.resume-left {
  width: 10%;
}
.resume-right {
  width: 40%;
  padding-left: 5%;
}
.resume-photo {
  width: 65%;
  margin: 5% 15%;
  border-radius: 50%;
}
.resume-name {
  margin-bottom: 50px;
}
ul {
  list-style-type: circle;
  padding-inline-start: 80px;
  margin-block-start: 0.5em;
}
.resume-item {
  position: relative;
  margin-block-end: 1em;
}
.resume-item-title {
  font-size: 1em;
}
.resume-section-header {
  color: #8b0000;
}
.resume-link {
  color: #4242d1;
  font-weight: bold;
}
.resume-item-position {
  padding-inline-start: 40px;
  font-style: italic;
  margin: 0.5em 0;
}
.resume-item-date {
  position: absolute;
  right: 0;
  top: 0;
}
.resume-download {
  margin: 40px 0;
}
.resume-download button {
  background-color: #2a2a2c;
  color: #dcdcdc;
  padding: 15px;
  border-radius: 4px;
  font-size: 1.25em;
}
/************KEYFRAMES*************/

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media only screen and (max-width: 1800px) {
  .resume-left {
    width: 15%;
  }
}
@media only screen and (max-width: 1480px) {
  .resume-left {
    width: 30%;
  }
  .resume-right {
    width: 50%;
  }
  .resume-container {
    margin: 1.5em 5%;
  }
}

@media only screen and (max-width: 1220px) {
  .resume-right {
    width: 60%;
  }

  .resume-item-date {
    position: relative;
  }
}

@media only screen and (max-width: 1080px) {
  .card-content {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .card-links {
    justify-content: center;
  }
  .hamburger {
    display: inline-block;
  }
  .navbar-full-screen {
    display: none;
  }
  .btn,
  .btn:link,
  .btn:visited {
    padding: 15px;
    text-align: center;
  }
  .navbar-mobile {
    display: flex;
  }
  .navbar {
    justify-content: flex-end;
    padding-right: 4.5em;
  }
}
@media only screen and (max-width: 960px) {
  .navbar {
    padding-right: 0;
    justify-content: center;
  }
  .heading-primary {
    font-size: 2.5em;
  }
  .jobTitle h1 {
    font-size: 1.5em;
  }
  .resume-container {
    flex-direction: column;
    margin: 1.5em 0;
    align-items: center;
  }
  .resume-left {
    width: 60%;
    padding-left: 5%;
  }
}
@media only screen and (max-width: 675px) {
  .card-links {
    justify-content: center;
  }
  .card-links .btn,
  .card-btn,
  .live {
    padding: 0 25px;
    margin-top: 15px;
  }
  .projectContainer {
    width: 80%;
  }
}

@media only screen and (max-width: 530px) {
  .heading-primary {
    font-size: 1.5em;
    font-weight: 700;
  }
  .jobTitle h1,
  .card-content h2 {
    font-size: 1.2em;
  }
  .card {
    text-align: center;
  }
  .languages {
    justify-content: center;
  }
  .card-thumbnail {
    display: none;
  }
}
